
@import '../../modules';


section.section-osp9115{
  text-align: center;
  padding-bottom: 14.8rem;
  div.tit{
    h3{
      padding-bottom: 4rem;
    }
    padding-bottom: 13.3rem;
  }

  div.mypage-signout{
    position: relative;
    display: inline-block;
    width: 663px;
    height: 284px;
    padding-top: 11.8rem;
    margin-bottom: 13rem;
    margin-right: 3rem;

    background-color: white;
    box-shadow: 0px 20px 50px rgba(0, 129, 255, 0.1);
    border-radius: 2rem;

    // " 아이콘
    &::before{
      position: absolute;
      top: -2.4rem;
      left: 4.4rem;
      width: 6rem;
      height: 6rem;
      content: '';
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      background-image: url('../../../../public/asset/icon/icon_quotes.svg');
    }

    p{
      letter-spacing: 0;
      @include style_font(400, 1.6rem, 1.4em, #666);
      strong{
        color: $color-2C99FF_azure;
        font-weight: 700;
      }
      b{
        font-weight: 700;
      }
    }

    span.img_signout{
      position: absolute;
      top: -17.4rem;
      right: -28.9rem;
      width: 40rem;
      height: 63.8rem;
      background-image: url('../../../../public/asset/images/img_bye.png');
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  // 메인으로 이동 버튼
  div.btn-area{
    button.btn{
      height: 6.4rem;

      justify-content: space-between;
      padding-right: 3.5rem;
      padding-left: 5.5rem;
      
      column-gap: 1rem;
      i.icon{
        margin-left: 0;
      }
    }
  }

}