// 🏠️ OSP-7112

@import '../../modules';

div.div-osp7112{
  padding-top: 8rem;
  text-align: center;
  padding-bottom: 13.6rem;
  div.search-result{
    height: 14.9rem;
    padding: 4rem 0;
    margin-bottom: 6rem;
    background-color: $bg_color-F8F8F8-grey;
    span{
      @include style_font(500, 2.4rem, 1, $font_color-212121_grey);
    }
    span.search-key{
      @include style_font(700, 2.4rem, 1, $color-2C99FF_azure);
      margin-bottom: 2rem;
    }
  }
  div.board-wrap{
    div.result{
      text-align: left;
      padding-bottom: 2.4rem;
      border-bottom: 1px solid #ececec;
      span{
        @include style_font(400, 2rem, 1, $font_color-212121_grey);
        &.res-cate{
          margin-right: 8px;
          font-weight: 700;
        }
      }
    }
    ul.cont-ul{
      padding: 0;
      margin-bottom: 4.8rem;
    }
  }
  div.board-wrap.notice{
    margin-bottom: 7.6rem;
  }
}

