@import './../modules';

header{
  position: fixed;  
  width: 100%;
  transition: background-color 0.3s;
  
  z-index: 999;

  // depth1
  .gnb__depth1{
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .--left{
      display: inline-flex;
    }
    .--right{
      display: inline-flex;
      justify-content: space-between;
      flex-wrap: nowrap;
    }

    .depth1__logo{
      display: inline-block;
      width: 100%;
      .logo{
        width: 14rem;
        height: 8.4rem;
        background-size: contain;
      }
      .logo::after{
        display: none;
      }
    }

    // 공통
    ul{
      display: inline-flex;
      align-items: center;

      li{
        width: max-content;
      }
      a{
        font-weight: 500;
      }
    }

    .depth1__sitemap{
      margin-left: 2.8rem;
      li{
        margin: 0 3rem;
      }
    }

    .depth1__community{
      gap: 5.8rem;
      margin-right: 6.3rem;
    }

    .depth1__user{
      gap: 2rem;

      a{
        @include style_font(400, 1.4rem, 1, #787878);
        margin: 0;
        
        &::after{
          // 밑에 줄 없애는거 
          display: none;
        }
        &.focused, &:hover{
          color: #666;
          font-weight: 500;
        }
      }
    }

    // 반응형시 나오는 박스
    .depth1__box{
      display: none;

      .btn-toggle-depth1{
        i.icon{
          width: 2rem;  
          height: 2rem;
        }
        // display: none;
      }
    }
  }

  // depth2
  .gnb__depth2{
    width: 100%;
    position: fixed;

    .depth2{
      color: $font_color-white;
      background-color: $color-10319D_royalazure;

      .depth2-container{
        display: flex;
        padding: 7.6rem 0;
        height: 38rem;

        div{
          margin-right: 239px;

          h5{
            padding-left: 10px;
            font-weight: 700;
            font-size: 24px;
            padding-bottom: 0.5em;
          }
          p{
            width: 266px;
            height: 15rem;
            padding-left: 10px;
            padding-bottom: 3em;
            @include style_font(300, 1.8rem, 1.8em, white);
          }
          button{
            width: 208px;
            height: 47px;
            background: #FFFFFF;
            border-radius: 5rem;
            font-weight: 700;
            color: $color-10319D_royalazure;
          }
        }

        ul{
          display: flex;
          flex-wrap: wrap;
          justify-content: flex-start;

          li{
            width: 386px;
            padding-bottom: 1em;
            margin: 20px 48px 40px 0;
            
            font-weight: 700;
            border-bottom: 1px solid rgba(255, 255, 255, 0.07);
            a{
              color: $font_color-white;
              
            }
          }
        }
      }

    }

    // 구름 커뮤니티
    .depth2:nth-child(5){
      background-color: $color-008CB2-commu-03;
    }

    // * 🦄️Interaction
    .depth2{
      display: none;
    }

    .depth2-open{
      display: block;
      @include animation(0s, 0.6s, fadeIn);
    }
    
    .depth2-close{
      @include animation(0s, 0.3s, fadeOut);
    }
    
  }
}

// header - 메인페이지
.header-main{
  background-color: transparent;

  .logo{
    background-image: url('../../../public/asset/icon/logo_white.svg');
  }

  .gnb__depth1{

    .depth1__sitemap li a,
    .depth1__community li a{
      @include style_header_gnb_a(white, 0.3s);
      padding: 0.5rem 0; 
      height: 2rem;

      color: white;
    }
    .depth1__community svg path{
      fill: white;
    } 
    .depth1__user a{
      color: rgba(255, 255, 255, 0.58);
      &:hover{
        color: rgba(255, 255, 255, 1);
      }
    }
  }
  
}

// header - 서브페이지
.header-sub{
  background-color: white;

  .logo{
    background-image: url('../../../public/asset/icon/logo_blue.svg');
  }

  .gnb__depth1 {
    a{
      @include style_header_gnb_a($font_color-black, 0.3s, $color-3855FF_ultramarine);
    }
    
    .depth1__community{
      // 버튼 - 구름 커뮤니티
      li:first-child a{
        &.focused, &:hover{
          @include style_header_gnb_a($font_color-black, 0.3s, $color-008CB2-commu-03);
        }
        &::after{
          background-color: $color-008CB2-commu-03;
        }
      }

      // 버튼 - 인재채용
      li:last-child a{
        svg path{
          fill: #212121;
        }
        &:hover svg path{
          transition: all 0.3s;
          fill: $color-3855FF_ultramarine;
        }
      }
    }
    .depth1__box{
      .btn-toggle-depth1{
        i.icon svg path{
          fill: #212121;
        }
      }
    }

  }

}


// * 👽️responsive
@include mobile-max{
  header{
    nav{
      width: 100%;
    }
    
    .gnb__depth1{
      padding: 0 1.6rem;
      height: 4.8rem;
      
      .depth1__logo{
        .logo{
          width: 7rem;
          height: 1.6rem;

        }
        
      }
      .depth1__sitemap,
      .depth1__community,
      .depth1__user{
        display: none;
      }

      .depth1__box{
        display: block;
      }

    }
    
  }
}
