// 🏠️ OSP-9121

@import '../../modules';

section.section-osp9121{
  padding-top: 4rem;
  padding-bottom: 15rem;
  background-color: $bg_main-F8F9FA;
  .width1352{
    width: 1352px;
    margin: 0 auto;
  }
  div.tool{
    margin-bottom: 2.4rem;
    text-align: right;
    button{
      margin-left: 1.4rem;
    }
  }
  div.per-day{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding-bottom: 5.6rem;
    h3{
      width: 100%;
      height: 4.4rem;
      margin-bottom: 4rem;
      @include style_font(700, 2.4rem, 1, $color-0063C0_azure-emp-02);
      i.icon{
        width: 4.4rem;
        height: 4.4rem;
        vertical-align: -0.4em;
        margin-right: 2rem;
      }
    }
    ul{
      width: 1288px;  
      // width: 95%;
      li{
        position: relative;
        padding: 5rem 6rem;
        margin-bottom: 4rem;
        letter-spacing: 0;
        border: 1px solid #F8F8F8;
        box-shadow: 0px 15px 40px rgba(45, 52, 74, 0.05);
        background-color: white;
        border-radius: 2rem;
        button{
          position: absolute;
          right: 3rem;
          top: 3rem;
          i.icon{
            width: 2rem;
            height: 2rem;
          }
        }
        h4{
          @include style_font(700, 2rem, 1.4em, $font_color-black);
          padding-bottom: 2.6rem;
          strong{
            color: $color-2C99FF_azure;
          }
        }
        p{
          height: 5.4rem;
          padding-right: 14rem;
          word-break: normal;
          overflow: hidden;
          @include style_font(400, 1.6rem, 1.6em, #666666);
        }
        div.cont{
          margin-top: 2.4rem;
          i.icon{
            background-image: none;
            margin-right: 1rem;
          }
          span{
            @include style_font(400, 1.6rem, 1, #B0B0B0);
            margin-right: 0.8rem;
          }
          span.views{
            color: $color-2C99FF_azure;
          }
        }
      }
    }
    div.last{
      margin-top: 5.6rem;
      width: 95%;
      color: $color-2C99FF_azure;
      i.icon{
        width: 2.4rem;
        height: 2.4rem;
        margin-right: 1rem;
        vertical-align: -0.2em;
      }
    }
  }
  div.per-day.--today{
    ul li{
      border: 1px solid #E9F3FF;
      box-shadow: 0px 10px 50px rgba(0, 129, 255, 0.1);
    }

  }
}
