@import '../../modules';


section.section-osp9113{
  background-color: white;

  div.mypage-wrap{
    background-color: $bg_main-F8F9FA;

    h4, h5{
      @include style_font(500, 2rem, 1.2em, #3C3C3C);
      margin-bottom: 2rem;
      i.icon{
        width: 2.4rem;
        height: 2.4rem;
        margin-right: 1.6rem;
      }
    }

    h5{
      padding-top: 6.8rem;
    }

    ul{
      flex-direction: column;
      margin-left: 5.6rem;
      li{
        list-style-type: disc;
        @include style_font(400, 1.6rem, 1.6em, #666);
      }
    }
  }
  div.tools{
    padding-bottom: 4rem;
    div.--l{
      a{
        margin-right: 2rem;
      }
    }
  }
}

section.section-osp9114{
  background-color: white;
  div.mypage-wrap{
    background-color: $bg_main-F8F9FA;
  }
}

