@import '../../modules';

// 공지사항 페이지
section.section-osp7116{
  div.board-wrap{
    padding-top: 0;
    padding-bottom: 9.6rem;
    box-shadow: none;
    div.title{
      span{
        height: 4rem;
        text-align: center;
        background-color: $bg_color-F8F8F8-grey;
        padding: 1rem 0;
        @include style_font(500, 1.6rem, 1.2em, $font_color-797979_grey);
        border-radius: 5px;
      }
    }
    ul.cont-ul{
      padding: 0;
    }
  }
} 
// osp7117 : 공지사항 상세 페이지 
div.div-osp7117{
  padding-top: 7.9rem;
  padding-bottom: 10.2rem;
  text-align: center;
  ul.cont-ul{
    margin-bottom: 0;
  }
  div.--dp2{
    min-height: 70.2rem;
    background-color: $bg_main-F8F9FA;
    padding: 4.4rem 4.8rem;
    // margin-bottom: 4.4rem;
    border-radius: 0 0 2rem 2rem;
    p{
      text-align: left;
      @include style_font(400, 2rem, 2em, #4b4b4b);
    }
  }
}