// 🏠️ OSP-8112

@import '../../modules';

section.osp8112{
  // 상단 배너 및 
  div.banner{
    position: relative;
    height: calc(42.5rem + 8.4rem);
    background-color: $bg_main-F8F9FA;
    padding-top: 8.4rem;

    // 뒷 배경 (원) 설정
    z-index: 0;
    overflow: hidden;
    &::after{
      position: absolute;
      content: '';
      height: 100%;
      z-index: -1;
      background-repeat: no-repeat;
      background-image: url('../../../../public/asset/images/res/bg_circle_1.svg');

      background-size: 1254px;
      width: 1254px;
      height: 1254px;
      top: -636px;
      right: -536px;
    }
    // 상단
    div.--top{
      padding-top: 11.1rem;
      h2{
        @include style_font(700, 3.8rem, 1, $font_color-black);
        padding-bottom: 3.7rem;
      }
      p{
        @include style_font(400, 2.4rem, 1, $font_color-black);
      }
    }
    // 하단
    div.--bottom{
      padding-top: 12rem;
      display: flex;
      justify-content: space-between;
      ul.toggle{
        li{

          display: inline-block;
          button{
            position: relative;
            margin-right: 3.3rem;
            @include style_font(400, 2.4rem, 1, #B0B0B0);
            &::after{
              position: absolute;
              content: '';
              width: 100%;
              height: 2px;
              bottom: -5px;
              left: 0;
              background-color: $color-2C99FF_azure;
              transform: scaleY(0%);
              transition: transform 0.1s;
            }
            transition: all 0.3s;
          }
          
          button.focused{
            color: $color-2C99FF_azure;
            font-weight: 700;
            &::after{
              transform: scaleY(100%);
            }
            // text-decoration: underline;
          }
        }
      }

      .dropbox{
        width: 33rem;

        label{
          color: $font_color-212121_grey;
        }
      }
    }
  }

  // 정책 내용
  div.content{
    // height: 55.5rem;
    padding-top: 5.2rem;
    padding-bottom: 14rem;
    p{
      @include style_font(400, 2rem, 2em, black);
      padding-bottom: 4rem;
    }
  }
}