@import '../../modules';

// section.section-osp9117{

.mypage{
  .tool-top{
    .switch-list{
      height: 3.6rem;
      align-items: center;
      margin-bottom: 4rem;
    }
  }

  .board-wrap{
    padding: 4.5rem 4.6rem;
    margin-top: 2.4rem;
    margin-bottom: 4.8rem;
    box-shadow: none;
    border-radius: 3rem;

    
    .board-cate{
      @include board_grid_mypage();
      text-align: center;
      li{
        @include style_font(500, 1.6rem, 1.2em, $font_color-797979_grey);
        padding: 1rem 0;
        height: 4rem;
        width: 100%;
        
        background-color: $bg_color-F8F8F8-grey;
        border-radius: 5px;
        input[type="checkbox"]{
          width: 2.4rem;  
          height: 2.4rem;
        }
      }
    }

    .board-cont{
      li{
        display: grid;
        grid-template-columns: 64px 1002px 152px 106px;
        align-items: center;
        gap: 8px;
        text-align: center;
        height: 9.5rem;
        border-bottom: 1px solid #ECECEC;

        input[type="checkbox"]{
          justify-self: center;
          width: 2.8rem;  
          height: 2.8rem;
        }
        a{
          display: flex;
          align-items: center;
          text-align: left;
          padding-left: 2rem;
          span.views{
            font-weight: 700;
            color: $color-2C99FF_azure;
          }
          i.icon{
            width: 2.4rem;
            height: 2.4rem;
            margin: 0 2px;
          }
        }
        h4{
          @include style_font(700, 2rem, 1, #212121);
          text-align: left;
        }
        p,span{
          @include style_font(400, 2rem, 1, #666666);
        }
      }
    }
  }
}





// section-osp9119
.section-osp9119{
  .board-wrap.--commt{ // 마이페이지 내가 쓴 글 > 댓글
    ul.board-cate{
      display: grid;
      grid-template-columns: 64px 1076px 192px;
      align-items: center;
      gap: 8px;
      text-align: center;
    }
    ul.board-cont li{
      display: grid;
      grid-template-columns: 64px 1076px 192px;
      align-items: center;
      gap: 8px;
      text-align: center;
      height: 9.5rem;
      border-bottom: 1px solid #ECECEC;
      a{
        letter-spacing: 0;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        div{
          display: flex;
          align-items: center;
        }
        i.icon{
          width: 1.6rem;
          height: 1.6rem;
          margin: 0.1rem;
        }
        h4, span{
          font-size: 1.6rem;
        }
        div.cont{
          margin-top: 0.4em;
          .icon.icon-text{
            margin-right: 1rem;
            background-image: none;
          }
          .icon.icon-img{
            background-image: none;
          }
          span, span.view{
            color: #B0B0B0;

          }
        }
      }
    }
  }
}