/** 
  * 2022.03.21 ~ 
  * @import reset-scss
  * @import modules

  ** normalize

  ** 💫️ icons
  ** 🎈 imgs
  *todo icon 이름 ico_ -> icon_
*/

@import 'scss-reset/_reset.scss'; // reset css style
@import 'modules'; // modules: 변수, mixin 패키지
@import 'module-icon';

// 스크롤 바 숨김
::-webkit-scrollbar{
  width: 0;
  height: 0;
}

html{
  font-size: 10px; // rem 기준
  scroll-behavior: smooth;
}

// * normalize
body{

  $font-stack: 'NotoSansKR-Regular';

  @include font-face('NotoSansKR-Regular', 'NotoSansKR-Regular', 400);
  
  font: 100% $font-stack;

  font-size: 18px;
  letter-spacing: -0.5px;
  line-height: 1;

  color: #2E2E2E;

  a{
    text-decoration: none;
    outline: none;
    color:inherit;
    &:hover, &:active{
      text-decoration: none; 
      background-color:transparent;
    }
  }
  span, i{
    display: inline-block;
  }
  input{
    border: 0;  
  }
  textarea{
    resize: none;
  }
}



// *🍚️ theme
.App{
  position: relative;
  min-width: 1440px;

  .width1440{ 
    width: $width_1440;
    margin: 0 auto;
  }

  .width1352{
    width: 135.2rem;
    margin: 0 auto;
  }

  .--mobileview{
    display: none;
  }

  .--h{
    visibility: hidden;
  }
  .--n{
    display: none;
  }

  // * global 지수빈
  .clear::after{
    clear: both;
    display: block;
    content: '';
  }
  .wrap-img{
    img{
      display: block;
      width: 100%;
      min-height: 100%;
    }
  }
  .date{
    color: #B0B0B0;
    font-size: 15px;
  }


  // * svg
  .svg{
    svg{
      width: inherit;
      height: inherit;
    }
  }




  // common-rule - checkbox
  input[type="checkbox"]{ 
    appearance: none;
    width: 2.4rem;
    height: 2.4rem;
    border: 1px solid #D8D8D8;
    border-radius: 3px;
    background-color: white;

    transition: all 0.3s;

    &:hover{
      border: 1px solid $color-2C99FF_azure;
    }

    &:checked{
      border: 1px solid $color-2C99FF_azure;

      background-color: $color-2C99FF_azure;
      background-image: url('../../public/asset/icon/icon_CheckBox.svg');
      background-size: cover;
      background-position: center;
    }
  }

  // * tab style1 
  div.tab-style_box1{
    display: flex;
    justify-content: center;
    align-items: center;

    padding-top: 9.6rem;
    padding-bottom: 3.2rem;
    ul{
      position: relative;
      display: flex;
      &::before{
        // 그냥 border주면 안맞음
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        border-radius: 1rem;
        border: 3px solid $line_color-ECECEC_grey;
        z-index: -9;
      }
      li{
        width: 29.3rem;
        text-align: center;
        // padding: 0 1.4rem;
        button, a{
          display: inline-block;
          padding: 2rem 8rem;
          @include style_font(700, 1.8rem, 1, $font_color-AFAFAF_grey);
        }
        &.--active{
          left: 10%;
          height: 60px;
          background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
          border-radius: 1rem;
          button, a{
            color: white;
          }
        }
      } 
    }
    .tab-focus{
      width: 33.33%;
      transform: translateX(0);
    }
  }


  // * 버튼 - basic theme
  .btn{
    display: inline-block;
    transition: all 0.3s;
  }

  .btn-basic-01{
    padding: 1.1rem 2rem;
    height: 4.2rem; 
    border-radius: 5px;
    &.--d{
      color: $font_color-B0B0B0_grey;
      border: 1px solid $font_color-B0B0B0_grey;
    }
    &.--a{
      color: #666666;
      border: 1px solid #666666;
      &:active{
        color: #212121;
        border: 1px solid #212121;
      }
    }
  }

  .btn-basic-02{
    display: inline-flex;
    padding: 1.1rem 2rem;
    height: 4.2rem; 
    border-radius: 5px;
    &.--d{
      background-color: #B0B0B0;
      color: white;
    }
    &.--a{
      color: white;
      background-color: $color-2C99FF_azure;
      &:hover{
        background-color: $color-007DF1_azure-emp-01;
      }
      &:active{
        background-color: $color-0063C0_azure-emp-02;
      }
    }

  }

  .btn-rounded-01{
    @include style_font(500, 1.8rem, 1, $color-2C99FF_azure);
    padding: 1.5rem 3rem 1rem 3rem;
    background: white;
    border: 1px solid $color-2C99FF_azure;
    border-radius: 4rem;
    
    .icon-down-svg svg{
      width: 2.4rem;
      height: 2.4rem;
      path{
        fill: $color-2C99FF_azure;
      }
    }
    &:hover{
      color: $color-007DF1_azure-emp-01;
      border: 1px solid $color-007DF1_azure-emp-01;
      .icon-down-svg svg path{
        fill: $color-007DF1_azure-emp-01;
      }
    }
    &:active{
      color: $color-0063C0_azure-emp-02;
      border: 1px solid $color-0063C0_azure-emp-02;
      .icon-down-svg svg path{
        fill: $color-0063C0_azure-emp-02;
      }
    }

  }

  .btn-rounded-02{
    @include style_font(700, 2.2rem, 1, $color-2C99FF_azure);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 1.5rem 3rem;

    background: white;
    border-radius: 4rem;
    transition: all 0.3s;
    color: $color-2C99FF_azure;
    &:hover{
      color: $color-007DF1_azure-emp-01;
    }
    &:active{
      color: $color-0063C0_azure-emp-02;
    }
  }


  .btn-rounded-02.commu{
    color: $color-0AB4E2_commu-01;
    &:hover{
      color: $color-06A1CB-commu-02;
    }
    &:active{
      color: $color-008CB2-commu-03;
    }
  }



  .btn-rounded-03{
    @include style_font(700, 2.2rem, 1.2, white);
    border-radius: 4rem;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    // height: 6.4rem;
    // padding: 1.65rem 3.5rem 1.5rem 5.5rem;
    background-color: $color-2C99FF_azure;
    transition: background-color 0.3s;
    i.icon{
      // width: 3.4rem;
      // height: 3.4rem;
      margin-left: 1rem;
      // vertical-align: -0.66em;
    }
    &:hover{background-color: $color-007DF1_azure-emp-01;}
    &:active{background-color: $color-0063C0_azure-emp-02;}
  }
  
  // 추후에 상태 업데이트 예정 
  .btn-rounded-03-grad{
    @include style_font(500, 1.8rem, 1.2, white);
    background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
    border-radius: 4rem;
    .icon-down-svg svg{
      width: 2.4rem;
      height: 2.4rem;
      path{
        fill: white;
      }
    }
  }
  


  // * 버튼 - 메뉴 박스 토글 (점 세 개)
  .btn-box-toggle{
    i.icon{
      width: 2.4rem;
      height: 2.4rem;
      opacity: 0.3;
      transition: opacity 0.3s;
    }
    &:hover{
      i.icon{
        opacity: 0.6;
      }
    }
    &:active{
      i.icon{
        opacity: 1;
      }
    }
  }  


  // * 버튼 - 더보기나 상세페이지 이동
  .btn-more{ 
    font-weight: 500;
    padding: 1rem 0;
    color: $color-0082F3_btn;
    text-align: center;

    .icon{
      margin-left: 6px;
    }

    &:hover{
      color: $color-007DF1_azure-emp-01;
      
      .icon{
        transition: transform 0.6s;
        
        @include animation(0s, 0.8s, moveSideToSide);
        animation-iteration-count: infinite;
      }

    }
    &:active{color: $color-0063C0_azure-emp-02;}
  }

  .btn-more.commu{ 
    @include style_font(500, 1.8rem, 1, $color-008CB2-commu-03);
    display: inline-block;
    color: $color-0AB4E2_commu-01;
    &:hover{color: $color-06A1CB-commu-02;}
    &:active{color: $color-008CB2-commu-03;}
  }

  // * 버튼 - commu theme

  .btn-basic-02.commu{
    &.--a{
      background-color: $color-0AB4E2_commu-01;
      &:hover{
        background-color: $color-06A1CB-commu-02;
      }
      &:active{
        background-color: $color-008CB2-commu-03;
      }
    }

  }
  .btn-rounded-01.commu{
    @include style_font(500, 1.8rem, 1, $color-0AB4E2_commu-01);
    background: #FFFFFF;
    border: 1px solid $color-0AB4E2_commu-01;
    border-radius: 4rem;
    .icon-down-svg{
      vertical-align: middle;
      width: 2.4rem;
      height: 2.4rem;
      margin-left: 1rem;
      svg path{
        fill: $color-0AB4E2_commu-01;
      }
    }
    &:hover{
      color: $color-06A1CB-commu-02;
      border: 1px solid $color-06A1CB-commu-02;
      i.icon svg path{
        fill: $color-06A1CB-commu-02;
      }
    }
    &:active{
      color: $color-008CB2-commu-03;
      border: 1px solid $color-008CB2-commu-03;
      i.icon svg path{
        fill: $color-008CB2-commu-03;
      }
    }
  }


  .btn-rounded-03.commu{
    @include style_font(500, 1.8rem, 1, white);
    padding: 1.5rem 3rem;
    background-color: $color-0AB4E2_commu-01;
    &:hover{
      background: #06A1CB;
    }
    &:active{
      background: $color-008CB2-commu-03;
    }
  }


  
  // * 🎈img 
  .img{
    display: block;
    background-repeat: no-repeat;
    background-size: cover;
  }
  
  // 고객문의 
  .img-customer-banner{
    background-image: url('../../public/asset/images/img_help_banner.png');
  }

  .img-no-result{
    background-image: url('../../public/asset/images/img_no_result.png');
  }
}


// loading
.loading{
  width: 100%;
  height: 100vh;

  display: flex;
  align-items: center;
  justify-content: center;

  .loading__container{
    -webkit-mask: url('../../public/asset/svg/loading_white_font.svg');
    mask: url('../../public/asset/svg/loading_white_font.svg');

    width: 22.4rem; 
    height: 3.3rem;

    .loading-tmaxos-color{
      width: 100%;
      height: 100%;
      
      background: linear-gradient(90deg, rgba(75, 165, 255, 0) 7.89%, #4BA5FF 45.72%, #4BA5FF 52.44%, rgba(75, 165, 255, 0) 93.42%);;
      background-size: 67% 100%;

      // background position으로 애니메이션 정의
      background-position-x: 0%;

      animation-name: loading;
      animation-timing-function: cubic-bezier(.65, 0,.65, 1); // ease-out ease-out
      animation-iteration-count: infinite;
      animation-duration: 3.2s;
    }
  }
}


// TEMP page 
div.helloTmax{
  width: 70vw;
  margin: 0 auto;
  text-align: center;
  h1{
    padding: 5rem 0;
    font-size: 3rem;
  }
  h2{
    padding: 2rem 0;
    line-height: 1.4em;
    font-size: 2rem;
  }
  p{
    padding: 2rem 0;
    font-size: 1.4rem;
    line-height: 1.6em;
  }
  ul{
    padding-top: 4rem;
    padding-bottom: 10rem;
    li{
      padding-top: 1rem;
      display: flex;
      justify-content: space-between;
      font-size: 16px;
      .response{
        color: blueviolet;

      }
      a{
        color: blue;
        &:hover{
          text-decoration: underline;
        }
      }
    }
  }
  hr{
    margin: 0.4rem 0;
    background-color: darkblue;
  }
}


// 반응형
@include desktop-max{

  body{
    width: 100vw;
  }
  .App{
    position: relative;
    min-width: 100vw;

    overflow: hidden;
    .width1440{
      width: 100%;
    }

  }
}

@include mobile-max{
  body{
    font-size: 1.2rem;
  }
  .App{
    .--pcview{
      display: none !important;
    }
    .--mobileview{
      display: initial;
    }
  
    .btn-more{ 
      .icon{
        margin-left: 6px;
        
        svg{
          width: 14px;
          height: 7px;
        }
      } 
    }
  }
}
